* {
  user-select: none;
}
html {
  scroll-behavior: smooth;
}
.logo {
  height: 100%;
}
.logo:hover {
  cursor: pointer;
}
.space {
  flex: 3;
}
.naviList {
  display: flex;
  align-items: center;
  font-size: 18px;
}
.naviList a {
  text-decoration: none;
  color: #fff;
}
.naviList a .navi {
  margin-left: 50px;
}
.naviList a:hover {
  text-decoration: underline;
}
.poster {
  height: calc(100vh - 64px);
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 -5px 10px rgba(0, 0, 0, 0.1) inset;
  background-color: #fff;
}
.poster .image {
  width: 40%;
}
.poster .title {
  margin-right: 100px;
  margin-bottom: 50px;
  font-size: 48px;
  line-height: 72px;
}
.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0px 300px 50px;
  background-color: #f5f5f5;
}
.main .block {
  width: 100%;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.main .block > * {
  width: 100%;
}
.main .block .title {
  text-align: center;
  font-size: 42px;
  color: #333;
}
.main .block .content {
  font-size: 16px;
  position: relative;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  line-height: 27px;
  padding: 20px 40px;
  margin-bottom: 20px;
  background-color: #fff;
  border-radius: 15px;
  transition: all 0.3s;
  cursor: pointer;
}
.main .block .content:hover {
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.1);
}
.main .block .content.text:hover {
  color: #fff;
  background-color: #2558ad;
  width: calc(100% + 600px);
  padding: 20px 340px;
  border-radius: 0;
}
.main .block .content p {
  width: 100%;
}
.main .block .content img {
  width: 100%;
  margin: 10px;
}
table {
  font-size: 16px;
}
th {
  color: #fff !important;
  background-color: #2558ad !important;
}
